*{
  box-sizing: border-box;
}
body{
  margin: 0;
  padding: 0;
  background-color: #eff3f7;
  height: 100%;
}
html,
body,
#root,
.container{
  height: 100%;
  display: flex;
  justify-content: center;
}
.loader{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loader_text{
  font-weight: bold;
}
.movies{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  padding: 50px;
  padding-top:70px;
  width: 80%;
}
.movies .movie{
  width: 45%;
  background-color: #fff;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 20px;
  position: relative;
  color:#adaeb9;
  -webkit-box-shadow: 12px 15px 16px 0px rgba(196,184,196,1);
-moz-box-shadow: 12px 15px 16px 0px rgba(196,184,196,1);
box-shadow: 12px 15px 16px 0px rgba(196,184,196,1);
}
.movies img{
  position: relative;
  top:-50px;
  max-width: 180px;
  width: 100%;
  margin-right: 30px;
  -webkit-box-shadow: 12px 15px 37px -28px rgba(18,14,18,1);
-moz-box-shadow: 12px 15px 37px -28px rgba(18,14,18,1);
box-shadow: 12px 15px 37px -28px rgba(18,14,18,1);
}
.movies .movie_title, .movie .movie_year{
  margin:0;
  font-weight: 300;
}
.movie .movie_title{
  margin-bottom: 5px;
  font-size: 24px;
  color:#2c2c2c;
  font-weight: bold;
}
.movie .genres{
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin:5px 0;
}
.movie .genres li{
  margin-right: 10px;
  font-size: 14px;
}